.tags {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    gap: 10px;
    margin-top: 20px;
  
    .tag {
      background-color: $primary-color;
      color: white;
      text-align: center;
      border-radius: 10px;
      font-size: 14px;
      padding: 4px 30px;
    }
  }